body {
  background-color: blanchedalmond;
  transition: 0.8s ease-out;
  color: #0c0c0c;
}

h1,
h2,
h3,
h4 {
  font-family: "Montserrat Subrayada", "Roboto", sans-serif;
}

p,
i,
a,
li {
  font-family: "Montserrat", "Open Sans", sans-serif;
}

.darkTheme {
  background-color:#0a1119;
  color:#dcdcc5;
  transition: 0.8s ease-out;
}

.App-div {
  display: flex;
  flex-direction: column;
}

header {
  margin: -0.5em 0em 0em -0.5em;
  padding-left: 1em;
  display: flex;
  width: 100%;
  height: 3.5em;
  justify-content: center;
  align-items: center;
  background-color: #fff7d9;
}

.navDarkTheme {
  background-color: #080d13;
}

.header-p {
  text-align: center;
  font-size: x-large;
  font-family: "Montserrat Subrayada", "Open Sans", sans-serif;
}

.moon,
.sun {
  position: relative;
  left: 1em;
  transition: 1s ease-out;
  width: 2.3em;
  border: 0.1em solid #050505;
  border-radius: 50%;
  box-shadow: 0px 0px 5px;
}

.moon {
  position: relative;
  left: 2em;
  transition: 1s ease;
}

footer {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 3em;
  background-color: #fff7d9;
  transition: 0.5s ease;
  z-index: 1;
}

footer:hover {
  height: 4em;
  transition: 0.5s ease;
}

footer:hover .logo {
  width: 4em;
  margin-right: 3em;
  transition: 0.5s ease;
}

footer:hover .logo2 {
  width: 2.5em;
  margin-right: 3em;
  transition: 0.5s ease;
}

.logo {
  width: 2.5em;
  margin-right: 1.5em;
  transition: 0.5s ease;
}

.logo2 {
  width: 2em;
  margin-right: 1.5em;
  transition: 0.5s ease;
}

.mainContent{
  width: 100%;
  padding-top: 3.5em;
  display: flex;
  justify-content: center;
  align-items: center;
}

.mainContent:hover .mainLogo{
  transform: translateX(-30px);
  transition: ease-in 0.5s;
}

.mainContent:hover .mainLogoMirror{
  background-color: blanchedalmond;
  opacity: 1;
  transform: translateX(30px);
  transition:  background-color ease-in 0.5s;
}

.mainLogo{
  width : 45em;
  transition: ease 0.5s;
  animation: start 0.65s ease;
}

@keyframes start {
  0% {
    transform: scale(1);
  }
  100%{
    transform: scale(0);
  }
}

.mainLogoMirror{
  position: absolute;
  width : 45em;
  clip-path: polygon(0% 100%, 0% 120%, 100% -21%, 100% 100%);
  transition: ease 0.5s;
  animation: start 0.65s ease;
  opacity: 0;
}

.loaderImage { 
  width: 10em;
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -5em 0 0 -5em;
}

.loader{
  width: 10em;
  height: 10em;
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -5em 0 0 -5em;
  animation: loaderRotate 2s ease-in-out infinite;
 
}

@keyframes loaderRotate {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.loader circle{
  width: 100%;
  height: 100%;
  fill: none;
  stroke-width: 28;
  stroke: blanchedalmond;
  stroke-dasharray: 410;
  stroke-dashoffset: 410;
  animation: loadCircle 2s ease-in infinite;
}

@keyframes loadCircle {
  0%, 100% {
    stroke-dashoffset: 410;
  }

  50% {
    stroke-dashoffset: 0;
  }

  50.1%{
    stroke-dashoffset: 820;
  }
}

section {
  background-color: blanchedalmond;
}

.services, .project, .contact {
  height: 90%;
  width: 100%;
  position:fixed;
  top:3.5em;
  transition: 0.65s ease-in-out;
}

.services-h2{
  position: relative;
  right:2em;
  background-color: #4175af;
  writing-mode:vertical-rl;
  margin-top: 0em;
  padding:0.2em 0 0.2em 0.3em;
  border-radius: 0.5em 0 0 0.5em;
  transition: 0.5s linear;
  animation:pop 0.65s ease; 
}

@keyframes pop {
  from {
    right: 0em;
  }
  
}

.project-h2{
  position: relative;
  right:2em;
  background-color: #4175af;
  writing-mode:vertical-rl;
  padding:0.2em 0 0.2em 0.3em;
  border-radius: 0.5em 0 0 0.5em;
  transition: 0.5s linear;
  animation:pop 0.65s ease; 
}

.contact-h2{
  position: relative;
  right:2em;
  background-color: #4175af;
  writing-mode:vertical-rl;
  padding:0.2em 0 0.2em 0.3em; 
  border-radius: 0.5em 0 0 0.5em;
  transition: 0.5s linear;
  animation:pop 0.65s ease; 
}

.services-p,
.project-p,
.contact-p {
  margin-left: 1em;
}

.services-illustration {
  margin-top: 5em;
  display: flex;
  justify-content: space-around;
}

.services-image{
  width: 50%;
}

.services-h3 {
  font-size: x-large;
  column-span: inherit;
}

.services-h1 {
  font-size: larger;
}

.services-text {
  padding-left: 4em;
  padding-right: 2.75em;
  columns: 2 25em;
  column-gap: 2em;
  text-align: justify;
  width: 50%;

}

/* border services */
.border {
  position: absolute;
  top: 50%;
  left:50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 80%;
  background-color: blanchedalmond;
  transition: 0.7s ease-in-out;
}

/* images project */
.project-illustration {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 6em;
}

.project-portfolio {
  height: 40em;
  display: flex;
}

.project-number  {
  width: 7em;
  overflow: hidden;
  transition: 0.35s ease-in;
  border-right: 0em solid #4175af;
}

.project-number:hover {
  width: 50em;
  transition: 0.6s 0.3s ease-out;
  border-right: 0.3em solid #4175af;
}

.project-1 >.project-images,
.project-3 >.project-images,
.project-5 >.project-images {
  height: 17.5em;
}

.project-2 >.project-images,
.project-4 >.project-images,
.project-6 >.project-images  {
  height: 18em;
}

.project-images {
  padding-left: 0.5em;
  width: 47em;
  border-radius: 0.5em;
  filter: drop-shadow(0px 0px 0px) blur(3px);
  transition: 0.6s ease-in-out;
}

.project-number:hover .project-images{
  filter: drop-shadow(8px 8px 8px #4175af) blur(0px);
}

.project-p {
  height: 0em;
  opacity: 0;
  transition: 0.35s ease-out;
}

.project-number:hover .project-p {
  height: auto;
  opacity: 1;
  transition: 0.35s ease-in;
}

.project-a{
  font-size: large;
  color: #4175af;
  text-decoration: none;
}

/*form*/
.contact-illustration {
  height: 40em;
  display: flex;
  justify-content: flex-start;
  margin-left: 4em;
  margin-top:4em;
}

.contact-form {
  display: flex;
  flex-direction: column;
}

.contact-input {
  position: relative;
  width: 40em;
  height: 3.8em;
  overflow: hidden;
}

.contact-input input{
  width:50%;
  height: 60%;
  color: #0c0c0c;
  padding-top: 2em;
  border: none;
  background-color: rgba(0,0,0,0);
  transition: background-color 0.5s ease-in;
}

.contact-input input:focus{
  background-color:#fff7d9;
}


.contact-input label {
  position: absolute;
  bottom: 0em;
  left:0;
  width: 50%;
  height: 100%;
  pointer-events: none;
  border-bottom: 0.1em solid darkblue;
}

.contact-input label::after{
  content:"";
  position: absolute;
  left:0;
  bottom: -0.1em;
  height: 100%;
  width: 100%;
  border-bottom: 0.3em solid #4175af;
  transform: translateX(-100%);
  transition: transform 0.3s ease-in;
}

.contact-name {
  position: absolute;
  bottom:0.3em;
  left:0;
  transition: all 0.3s ease-in;
}

.contact-input input:focus + .contact-label .contact-name,
.contact-input input:valid + .contact-label .contact-name {
  transform: translateY(-100%);
  font-size: larger;
  font-weight: bold;
  color: #4175af;
}

.contact-input input:focus + .contact-label::after,
.contact-input input:valid + .contact-label::after {
  transform: translateX(0);
}

.contact-select{
  margin-left: 1em;
  border: 0.1em solid #0c0c0c;
  border-radius: 0.5em;
  color: #4175af;
  background-color: #fff7d9;
  font-weight: 500;
  font-size: medium;
}

textarea {
  font-weight: 500;
  font-size: large;
  border: 0.1em solid #0c0c0c;
  border-radius: 0.5em;
  color: #4175af;
  background-color: #fff7d9;
}

.contact-button {
  font-weight: 500;
  font-size: medium;
  margin-top:1em;
  width: 50%;
  border-radius: 0.5em;
  color: #0c0c0c;
  background-color: #4175af;
}

/*login*/
.login-form{
  margin-left: 1em;
}

.login-button,
.back-button {
  font-weight: 500;
  font-size: medium;
  margin-top:1em;
  width: 20em;
  border-radius: 0.5em;
  color: #0c0c0c;
  background-color: #4175af;
}

/* phone */
@media screen and (max-width: 540px) {

  .mainLogo{
    margin-top: 30%;
    width : 20em;
  }

  .mainLogoMirror{
    display: none;
  }

  .services-illustration, 
  .project-illustration, 
  .contact-illustration {
    margin-top: 0em;
  }

  /*SERVICES*/
  .border {
    position: absolute;
    top: 50%;
    left:0;
    transform: translate(0%, -50%);
  }

  .services-image{
    display: none;
  }

  .services-text{
    padding-left: 0.75em;
    overflow-y: auto;
    max-height: 35em;
    columns: unset;
    width: auto;
  }

  .services-h3 {
    font-size: 1.35em;
  }

  /*PROJETS*/
  .project-illustration {
    justify-content: start;
    margin-left: 0.5em;;
  }

  .project-portfolio {
    display: flex;
    flex-wrap: wrap;
    height: 27em;
  }

  .project-number  {
    width: 6em;
    height: 0em;
    margin-bottom: 10em;
    overflow: visible;
    transition: 0.35s ease-in;
    border-right: 0em solid #4175af;
  }

  .project-number:hover  {
    width: 6em;
    border-right: none;
  }

  .project-number > h3 {
    font-size: medium;
  }

  .project-p {
    display: none;
  }

  .project-a {
    padding-top: 1em;
  }

  .project-number >.project-images {
    height: 5em;
  }

  .project-images {
    padding-left: 0.5em;
    padding-bottom: 0.75em;
    width: 5em;
    border-radius: 1em;
    filter: drop-shadow(5px 5px 8px #4175af) blur(0px);
    transition: 0.6s ease-in-out;
  }

   /*CONTACT*/
  .contact-illustration {
    margin-left: 1.5em;
  }

  .contact-textarea {
    width: 50%;
  }

}